.styles_container__3zGLV{
    background: #9DBEB9;
    width: 100%;
    height: 100vh;
    overflow: scroll;
}

header {
    display: flex;
    align-items: center;
    flex-direction: column;
    font-family: 'Montserrat';
    color: #194350;
}

header h2{
    margin: 0.5em;
}

header h5{
    margin: 0em;
}

.styles_containerForm__vnm31{
    display: flex;
    justify-content: center;
    margin-top: 1.5em;
}

.styles_form__2KQW_ {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 20em;
    color: #194350;
}

.styles_form__2KQW_ button {
    margin-top: 0.5em;
    background: #FF8882;
    cursor: pointer;
    border: #FF8882 solid;
    color: white;
}

.styles_form__2KQW_ button:hover{
    background: #ff5d54;
    border: #ff5d54 solid;
}

.styles_form__2KQW_ input {
    border: #194350 solid 0.15em;
    outline: none;
}

.styles_form__2KQW_ input:hover{
    border: #ff5d54  solid 0.15em;
}

